import Fps from "./ui/fps.js";

class Main {
    constructor(eleId){
        let mainBody = document.getElementById(eleId);
        this.fps = new Fps(mainBody);
    }

    run(){
        this.fps.startLoop();
    }
}

window.Monk = Main;


